$primary-color:#Fdff83;
$self_color:#d4cced;
$future_color:#30e3df;

.user_wrap{
    margin-top: 20vh;

    .row_wrap{
        justify-content: space-between;
        margin-bottom: 10px;
        align-items: center;
    }
    .info_wrap{
        text-align: center;
        .info_title{
            border-radius: 30px 30px 0 0;
            background-color: #fff;
            text-align: center;
            padding: 10px 0;
            font-size: 18px;
        }
        .border_future{
            border: 3px solid $future_color;
        }
        .border_self{
            border: 3px solid $self_color;
        }

        .info_content{
            border-radius: 0 0 30px 30px;
            background-color: #fff;
            text-align: center;
            padding: 25px 0;
            font-size: 18px;
        }

        .info_img{
            width: 65%;
            height: 150px;
            border-radius: 50%;
            border: 3px solid $primary-color;
            text-align: center;
            margin: 0 auto;
        }
        .toEdit_btn{
            width: 65%;
            text-align: center;
            border-radius: 30px;
            padding: 5px 0;
            background-color: #fff;
            border: 1px solid $primary-color;
            box-shadow: 0 4px 4px rgba(0,0,0,0.25);
            margin: 5px auto 0;
        }

        .toEdit_btn:active{
                box-shadow: none;
                transform: translateY(4px);
        }

    }
}
